
import {defineComponent} from 'vue';
import router from "@/router";

export default defineComponent({
  name: 'App',
  components: {},
  data() {
    return {
      router
    }
  },
  computed: {
    routeList() {
      let routes = [] as object[];

      router.getRoutes().forEach(function (route) {
        if (route.meta.isMenu) {
          routes.push(route);
        }
      });

      return routes;
    }
  }
});
